// Color Palette
// Based on the official Sass styleguide
// ===================================================================
// Descriptive colors
// ===================================================================
$white: #fff;
$black: #2e3131;
$transparent-white: rgba(255, 255, 255, 0.6);

.color-transparent-white {
  color: $transparent-white;
}
.bg-transparent-white {
  background-color: $transparent-white;
}
.color-white {
  color: $white;
}
.color-black {
  color: $black;
}

// ===================================================================
// Theme colors
// ===================================================================

$blue: #3a539b;
$blue-light: #00b5cc;

.color-blue {
  color: $blue;
}
.bg-blue {
  background-color: $blue;
}
.color-blue-light {
  color: $blue-light;
}
.bg-blue-light {
  background-color: $blue-light;
}

// Gradient colors
$gradient-start: #49a09d;
$gradient-end: #5f2c82;

.color-gradient-start {
  color: $gradient-start;
}
.color-gradient-end {
  color: $gradient-end;
}

