#steps {
  width: 100%;
  margin: 10px auto; }

.step {
  width: 40px;
  height: 40px;
  background-color: white;
  display: inline-block;
  border: 4px solid;
  border-color: transparent;
  border-radius: 50%;
  color: #cdd0da;
  font-weight: 600;
  text-align: center;
  line-height: 26px; }

.step:first-child {
  line-height: 30px; }

.step:nth-child(n + 2) {
  margin: 0 0 0 120px;
  transform: translate(0, -4px); }

.step:nth-child(n + 2):before {
  width: 100px;
  height: 3px;
  display: block;
  background-color: white;
  transform: translate(-120px, 15px);
  content: ""; }

.step:after {
  width: 180px;
  display: block;
  transform: translate(-70px, 3px);
  color: #818698;
  content: attr(data-desc);
  font-weight: 700;
  font-size: 13px;
  margin-top: 10px; }

.step:first-child:after {
  transform: translate(-70px, -1px); }

.step.active {
  border-color: #73b5e8;
  color: #73b5e8; }

.step.active:before {
  background: linear-gradient(to left, #58bb58 0%, #73b5e8 100%); }

.step.active:after {
  color: #73b5e8; }

.step.done {
  background-color: #58bb58;
  border-color: #58bb58;
  color: white; }

.step.done:before {
  background-color: #58bb58; }

@media (max-width: 767.98px) {
  #steps {
    display: none; } }

